import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';

class Page extends PureComponent {
    displayName = 'Page';
    
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>Avatar </h1>
                <blockquote className='page-tip'>
                    <br/>
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo1></Demo1>
                    </Label>
                    <Label className='code-box'>
                        <Demo3></Demo3>
                    </Label>
                </Label>

                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo2></Demo2>
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>Avatar</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'size', desc: '', type: 'string', default: ''},
                    {param: 'icon', desc: '', type: 'string', default: ''},
                    {param: 'font', desc: '', type: 'string', default: ''},
                    {param: 'src', desc: '', type: 'string', default: ''},
                    {param: 'shape', desc: ' square ', type: 'string', default: ''}
                ]}></Table>
            </div>
        );
    }
}

export default Page;



